import React, { useEffect, useState } from "react";
import { PlayCircleFilled, BackwardFilled } from "@ant-design/icons";
import player from "../../video/123.mp3";
import { Image } from "antd";
//import dayjs from "dayjs";
const HomePage = () => {
    const [userInput, setUserInput] = useState("");
    const dateFormat = "YYYY/MM/DD";
    const MM = ["", "", "", "", "", "", "", "", "", "", "", ""];
    const DD = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
    ];
    const geshihua = (number) => {
        return number > 9 ? "" + number : "0" + number;
    };
    const data = () => {
        let newdata = new Date();
        let olddata = new Date(new Date().getTime() - 24 * 86400000);
        let daojishi =
            new Date("2022/12/28").getTime() - new Date("2022/12/29").getTime() > 0
                ? ` ${geshihua(23 - olddata.getHours())}${geshihua(60 - olddata.getMinutes())}${geshihua(60 - olddata.getSeconds())}`
                : "fnished";
        return [
            `${newdata.getFullYear()}${geshihua(newdata.getMonth() + 1)}${geshihua(newdata.getDate())} ${geshihua(
                newdata.getHours()
            )}${geshihua(newdata.getMinutes())}${geshihua(newdata.getSeconds())}`,
            `${olddata.getFullYear()}${MM[olddata.getMonth()]}${DD[olddata.getDate()]}`,
            daojishi,
        ];
    };
    useEffect(() => {
        const boxes = document.querySelectorAll(".box");
        //wheel 
        //scroll 
        document.getElementById("main").addEventListener("scroll", function () {
            console.log(boxes);
            const triggerBottom = (window.innerHeight / 5) * 4 - 100;
            boxes.forEach((box) => {
                //getBoundingClientRect
                const boxTop = box.getBoundingClientRect().top;
                const boxBottom = box.getBoundingClientRect().bottom;
                console.log(boxTop);
                if (boxBottom < 150 || boxTop < 150) {
                    box.classList.add("opacity");
                } else {
                    box.classList.remove("opacity");
                }

                if (boxTop < triggerBottom) {
                    box.classList.add("show");
                } else {
                    box.classList.remove("show");
                }
            });
        });
    }, []);
    const [dataArray, setdataArray] = useState([" ", " ", ""]);
    const [bofang, setbofang] = useState(true);
    const [happy, sethappy] = useState(true);

    useEffect(() => {
        //setdataArray(data());

        setInterval(() => {
            setdataArray(data());
        }, 1000);
    }, []);
    useEffect(() => {
        bofang ? document.getElementById("video1").play() : document.getElementById("video1").pause();
    }, [bofang]);
    return (
        <div className="main" id="main">
            <div className="center" style={{ marginTop: "16px" }}>
                <div style={{ position: "fixed", top: 0, right: 0, width: "100%", textAlign: "center", zIndex: 2, marginTop: "16px" }}>
                    <h1>{dataArray[0]}</h1>
                    <h2>{dataArray[1]}</h2>
                    <h2>{dataArray[2] === "fnished" ? "" : "" + dataArray[2]}</h2>
                    {happy ? (
                        <div>
                            <audio id="video1" loop="loop">
                                <source src={player} type="audio/mp3" />
                            </audio>
                            {bofang ? (
                                <PlayCircleFilled
                                    onClick={() => {
                                        setbofang((i) => !i);
                                    }}
                                    style={{ fontSize: "30px" }}
                                />
                            ) : (
                                <BackwardFilled
                                    style={{ fontSize: "30px" }}
                                    onClick={() => {
                                        setbofang((i) => !i);
                                    }}
                                />
                            )}
                        </div>
                    ) : null}
                </div>

                {happy ? (
                    <div className="body" id="body">
                        <div id="box" style={{ marginTop: "70px", height: "400px" }} className="box show">
                            <h3 style={{ margin: "10px 0" }}></h3>
                            <p style={{ margin: "10px 0" }}></p>
                            <p>()</p>
                            <Image src="https://img.xiangmu.com/uploadpic/201710/07/0715370591395754.gif" />
                        </div>
                        <div id="box" className="box show">
                            <h3></h3>
                            <h4></h4>
                            <h4></h4>
                            <h4></h4>
                        </div>
                        <div id="box" className="box">
                            <h3></h3>
                            <h4></h4>
                            <h4></h4>
                            <h4></h4>
                            <h4></h4>
                        </div>
                        <div id="box" className="box">
                            <h3></h3>
                            <h4></h4>
                            <h4>
                                <span style={{ fontSize: "25px" }}></span>
                            </h4>
                        </div>
                        <div id="box" className="box">
                            <h3> </h3>
                            <h4></h4>
                            <h4></h4>
                            <h4></h4>
                        </div>
                        <div id="box" className="box">
                            <h3></h3>
                            <h4></h4>
                            <h4></h4>
                            <h4></h4>
                        </div>
                        <div id="box" className="box">
                            <h3></h3>
                        </div>
                        <div id="box" className="box">
                            <h3></h3>
                        </div>
                    </div>
                ) : (
                    <div className="body" id="body">
                        <div id="box" className="box show">
                            <h3></h3>
                            <h4></h4>
                        </div>
                        <div id="box" className="box">
                            <h4>....</h4>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default HomePage;
